    place-self: center;
    aspect-ratio: 16 / 9;
    height : 150px;
    place-content: center;
    background-image: url(/static/media/upload_image_background.20b410ac.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 150px 150px ;

}
#submit{
    grid-area: submit;
    align-self:center;
    width:100px;
    height: 40px;
    border-radius: 20px;
    border-width: 4px;
    border-style: inset;
    place-self: center;
    border-color: #0334dd;
    font-size: 20px;
    font-weight: 500;
}#blog_title , #blog_discription,.editing_pane #text_input{
    font-size: 15px;
    border-radius: 7px;
    border-color:#0334dd;
    border-width: 1px;
    padding-left: 7px;
    padding-right: 7px;
    font-family: monospace;
    transition-property: all;
    transition-duration: 0.5s;
    background-color: rgba(201, 201, 201, 0.219);
}
#blog_title:focus , #blog_discription:focus ,.editing_pane #text_input:focus{
    box-shadow: 0 0px 5px rgb(0, 110, 255) ,inset 2px 2px 5px #898989a3;
}
#submit:hover{
    box-shadow: 0 0 5px rgb(0, 110, 255)  ;
}
#submit:active{
    box-shadow: inset 2px 2px 5px rgba(44, 44, 44, 0.671);
    font-size: 19px;
}
#image_span{
    display: block;
    height: 100%;
    width: auto;
    background-repeat: no-repeat;
}
.editor{
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top:0;
    overflow:clip;
}
div.tabs{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex: auto;
            flex: auto;
    background-color:rgb(218 234 251 / 13%);
    color:#8e8e8ecc;
    font-weight: 500;
    border-top-left-radius:20px;
    border-top-right-radius:20px;
    margin-top: 30px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    place-items: center;
    box-shadow: inset 7px 6px 11px 2px #7196da57, inset -3px 4px 5px 2px #7196da57;
    font-size: 20px;
}
input[type="radio"]:checked + #label_editor,input[type="radio"]:checked + #label_preview{
    color:rgb(0, 107, 247);
    background-color: #ffffff8f;
    box-shadow: inset 3px 4px 0px 1px #b5b5b5;
    text-shadow: 3px 2px 4px #00e7ff38;

}

.editor >div:not(.tabs){
    min-height: 100%;
    height: calc(100% - 80px);
    display: grid;
    overflow: auto;
}
div.tabs label{
    border-top-left-radius:20px;
    border-top-right-radius:20px;
    padding-left:20px;
    padding-right:20px;
    margin-top: 5px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;

}.label_ico{
    width: 30px;
    margin-right: 5px;
    -webkit-filter: grayscale(1) contrast(0.5);
            filter: grayscale(1) contrast(0.5);
}
input[type="radio"]:checked + label > .label_ico{
    -webkit-filter:none;
            filter:none;
}
.editing_pane{
    width: 100%;
    height: calc(100% - 80px);;
    display: grid;
    background-color:rgb(218 234 251 / 13%);
    font-weight: 500;
    border-bottom-left-radius:20px;
    border-bottom-right-radius:20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    place-items: center;
    box-shadow: inset 7px -9px 11px 2px #7196da57, inset -3px -7px 5px 2px #7196da57;
    margin-bottom: 15px;
    position: absolute;
}
.editing_pane #text_input{
    margin-left:5px;
    margin-right:5px;
    width: 80%;
    resize: none;
    grid-area: textarea;
    height: 100px;
    border-style: solid;
    overflow: auto;
    display: grid;
    grid-template-columns: 1fr 50px;
    grid-template-rows: 20px 1fr;
    grid-template-areas:  "tag insert"
                          "maintext insert";
    padding: 0;
}
.components{
    -webkit-flex-direction: column;
            flex-direction: column;
    border-style: solid;
    border-width: 2px;
    border-radius: 20px;
    border-color: #0d19ff;
    min-height: 200px;
    min-width: 180px;
    background-color:#cfbf2b;
    max-height: 300px;
    overflow: auto;
    -webkit-backdrop-filter: blur(10px) saturate(1.5);
            backdrop-filter: blur(10px) saturate(1.5);
}

@media only screen and (min-width : 768px){
    .editing_pane{
        grid-template-columns: 70% 30%;
        grid-template-rows: 1fr 200px;
        grid-template-areas: "editing   components"
                             "editing   textarea";
    }
    .editing{
        width: 100%;
        height: 100%;
        overflow: auto;
        max-width: 600px;
        grid-area: editing;
    }
    .addComponents{
        display: none;
    }
    .components{
        grid-area: components;
        display: -webkit-flex;
        display: flex;
        -webkit-transform-origin: center;
                transform-origin: center;
    }.showpreview .blog_preview{
        width:700px;
        display: block;
    }
}
@media only screen and (max-width : 768px){
    .editing_pane{
        grid-template-rows: 1fr 120px;
        grid-template-columns: 100%;
        grid-template-areas: "editing" "textarea";
    }
    .editing{
        width: 100%;
        height: 100%;
        overflow: auto;
        grid-area: editing;
    }
    .editing_pane #text_input{
        height: 70px;
        max-height: 100px;
    }
    .addComponents{
        display: block;
        position: absolute;
        bottom: 130px;
        width: 40px;
        height: 40px;
        right: 0;
        margin-bottom: 10px;
        margin-right: 8vw;
        border-radius: 100%;
        border-color: #0093ff;
        border-width: 3px;
        background-image: url(/static/media/addComponents.b5f293f4.svg);
        background-size: cover;
    }.components{
        display: none;
        position: fixed;
        pointer-events: all;
        -webkit-transform: translateX(calc(50% - 10px )) translateY(calc(50% - 10px ));
                transform: translateX(calc(50% - 10px )) translateY(calc(50% - 10px ));

    }.showpreview .blog_preview{
        width:calc(100vw - 50px);
        display: block;
    }
}.showpreview .blog_preview{
